.portfolio-wrapper {
  .tab-text {
    color: #631f48;
  }
  .content {
    max-height: 450px;
    // height: auto;
    .thumb {
      position: relative;
      border-radius: 18px;
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
    }
    a {
      position: absolute;
      top: 0;
      left: 15px;
      right: 15px;
      border-radius: 18px;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.6);
      opacity: 0;
      transition: opacity 0.3s;
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
    }

    span {
      position: absolute;
      top: 0;
      left: 15px;
      right: 15px;
      border-radius: 18px;
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.6);
      opacity: 0;
      transition: opacity 0.3s;
      box-shadow: 0 4px 15px rgba(0, 0, 0, 0.5);
      cursor: pointer;
    }

    span:hover {
      opacity: 1;
      cursor: pointer;
    }
  }
}
