$text-color: #631f48;
.menu-bar-wrapper {
  .nav-link {
    color: $text-color !important;

    .selected {
      color: white !important;
      background-color: #007bff;
    }
    .menu-item {
      color: $text-color;
      padding: 7px;
      border-radius: 7px;
      text-decoration: none;
      &:hover {
        color: white;
        background-color: #007bff;
      }
    }
  }
}
