@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600;700&display=swap);
html,
body,
#root {
  margin: 0 !important;
  font-family: "Poppins" !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100% !important;
  background-image: url(/static/media/bg5.4694d0d6.png);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: cover;
  color: #631f48 !important;
  /* color: #cccccc !important; */
  /* overflow: hidden; */
}

@media (min-width: 575.98px) {
  html,
  body,
  #root {
    overflow: hidden;
  }
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* width */
::-webkit-scrollbar {
  width: 5px;
  border-radius: 20px;
}

/* Track */
::-webkit-scrollbar-track {
  /* background: #f1f1f1; */
  border-radius: 20px;
  margin-bottom: 55px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgba(99, 31, 72, 0.4);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgba(99, 31, 72, 1);
}

.menu-bar-wrapper .nav-link{color:#631f48 !important}.menu-bar-wrapper .nav-link .selected{color:white !important;background-color:#007bff}.menu-bar-wrapper .nav-link .menu-item{color:#631f48;padding:7px;border-radius:7px;text-decoration:none}.menu-bar-wrapper .nav-link .menu-item:hover{color:white;background-color:#007bff}

.wrapper{margin-bottom:50px}.wrapper hr{max-width:352px;border:1px solid #631f48;border-radius:2px}.wrapper h3{font-weight:700;font-size:25px}

.label{font-weight:700;font-size:15px}.label p{margin:auto auto auto 0}.value{font-weight:400;font-size:15px}

.timeline{padding:46px 0;position:relative}.timeline:before{content:"";position:absolute;top:0px;left:calc(31% + 15px);bottom:85px;width:4px;background:#631f48}.timeline:after{content:"";display:table;clear:both}.entry{clear:both;text-align:left;position:relative}.entry .title{margin-bottom:0.5em;width:33%;padding-right:30px;text-align:right;position:relative}.entry .title:before{content:"";position:absolute;width:15px;height:15px;border:4px solid salmon;background-color:#fff;border-radius:100%;top:15%;right:-9px;z-index:99}.entry .title h3{margin:0;font-size:16px;color:#631f48}.entry .title p{margin:0;font-size:100%}.entry .body{margin:0 0 3em;float:right;width:66%;padding-left:30px}.entry .body p{line-height:1.4em}.entry .body p:first-child{margin-top:-40px;font-weight:400}.entry .body ul{color:#aaa;padding-left:0;list-style-type:none}.entry .body ul li:before{content:"–";margin-right:0.5em}.t-title{font-weight:700 !important;font-size:16px !important;color:#631f48;margin-bottom:5px}.t-body{font-weight:400 !important;font-size:14px !important;line-height:25px !important;color:#631f48}@media only screen and (max-width: 576px){.entry .title:before{right:-17px !important}}@media only screen and (max-width: 992px) and (min-width: 576px){.entry .title:before{right:-15px !important}}@media only screen and (max-width: 1200px) and (min-width: 992px){.entry .title:before{right:-11px !important}}

.progress-wrapper{margin-bottom:13px}.progress-wrapper .progressbar{height:10px}.progress-wrapper p{font-weight:500}

.contact-wrapper p{font-weight:500;font-size:18px}.contact-wrapper .icon{height:32px;width:32px;color:yellow}.contact-wrapper .icon:hover{cursor:pointer}

.portfolio-wrapper .tab-text{color:#631f48}.portfolio-wrapper .content{max-height:450px}.portfolio-wrapper .content .thumb{position:relative;border-radius:18px;box-shadow:0 4px 15px rgba(0,0,0,0.2)}.portfolio-wrapper .content a{position:absolute;top:0;left:15px;right:15px;border-radius:18px;bottom:0;background-color:rgba(0,0,0,0.6);opacity:0;transition:opacity 0.3s;box-shadow:0 4px 15px rgba(0,0,0,0.5)}.portfolio-wrapper .content span{position:absolute;top:0;left:15px;right:15px;border-radius:18px;bottom:0;background-color:rgba(0,0,0,0.6);opacity:0;transition:opacity 0.3s;box-shadow:0 4px 15px rgba(0,0,0,0.5);cursor:pointer}.portfolio-wrapper .content span:hover{opacity:1;cursor:pointer}



.welcome-page img{width:250px;height:250px}.welcome-page h4{font-weight:500;font-size:20px;padding-left:2px;margin:0}.welcome-page h1{font-weight:700;font-size:48px;margin:0}.welcome-page h3{font-weight:700;font-size:25px}.welcome-page p{font-weight:400;font-size:16px;text-align:center}.welcome-page .icon{height:32px;width:32px;color:yellow}.welcome-page .icon:hover{cursor:pointer}@media (max-width: 575.98px){.welcome-page h1{font-size:30px}.welcome-page h3{font-size:20px}.welcome-page p{font-size:14px}}

.page-wrapper{padding-bottom:40px;padding-top:40px;height:100%;max-height:100%}.page-wrapper .contentWrapper{padding-top:20px;min-height:100%;background-color:rgba(255,255,255,0.5);-webkit-backdrop-filter:blur(2px);backdrop-filter:blur(2px);border-radius:30px;padding-bottom:40px;box-shadow:0px 8px 24px rgba(0,0,0,0.15)}.page-wrapper .contentWrapper .content{height:76vh;overflow-y:auto;padding-top:50px}

