.welcome-page {
  img {
    width: 250px;
    height: 250px;
  }
  h4 {
    font-weight: 500;
    font-size: 20px;
    padding-left: 2px;
    margin: 0;
  }

  h1 {
    font-weight: 700;
    font-size: 48px;
    margin: 0;
  }

  h3 {
    font-weight: 700;
    font-size: 25px;
  }

  p {
    font-weight: 400;
    font-size: 16px;
    text-align: center;
  }

  .icon {
    height: 32px;
    width: 32px;
    color: yellow;
    &:hover {
      cursor: pointer;
    }
  }

  @media (max-width: 575.98px) {
    h1 {
      font-size: 30px;
    }
    h3 {
      font-size: 20px;
    }
    p {
      font-size: 14px;
    }
  }
}
